import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnDelete } from 'components/common/button';
import { Card, Row, Table, Popconfirm, Col, Tabs, Input, Button } from 'antd';
import { insertScore } from 'dump/data';
import CreditClass from './CreditClass';
import ExamRoom from './ExamRoom';

const { TabPane } = Tabs;

const columns = [
  {
    title: 'Mã sinh viên',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Không đủ ĐK',
    dataIndex: 'notCondition',
    key: 'notCondition',
  },
  {
    title: 'CC (10)',
    dataIndex: 'cc_ten',
    key: 'cc_ten',
  },
  {
    title: 'KT1 (30)',
    dataIndex: 'kt_one',
    key: 'kt_one',
  },
  {
    title: 'TBCBP',
    dataIndex: 'tbcbp',
    key: 'tbcbp',
  },
  {
    title: 'Thi (60)',
    dataIndex: 'thi_60',
    render: () =>
      insertScore.length >= 1 ? (
        <Fragment>
          <Input />
        </Fragment>
      ) : null,
  },
  {
    title: 'TBC MH',
    dataIndex: 'tbc_mh',
    key: 'tbc_mh',
  },
  {
    title: 'Điểm số',
    dataIndex: 'numScores',
    key: 'numScores',
  },
  {
    title: 'Điểm chữ',
    dataIndex: 'letterScores',
    key: 'letterScores',
  },
  {
    title: 'Ghi chú',
    dataIndex: 'note',
    render: () =>
      insertScore.length >= 1 ? (
        <Fragment>
          <Input />
        </Fragment>
      ) : null,
  },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) =>
      insertScore.length >= 1 ? (
        <Fragment>
          {/* <BtnEdit href={`#credit-class/form/edit/${record.code}`} /> */}
          <Popconfirm 
            title="Bạn có muốn xóa?" 
            okText="Xóa"
            cancelText="Hủy"
            onConfirm={() => this.handleDelete(record.key)}
          >
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class InsertScorePage extends Component {
  state = {};

  render() {
    return (
      <Card title="Nhập điểm">
        <Row>
          <Col span={24}>
            <Tabs defaultActiveKey="1">
              <TabPane tab="Theo lớp tín chỉ" key="1">
                <CreditClass />
              </TabPane>
              <TabPane tab="Theo phòng thi" key="3">
                <ExamRoom />
              </TabPane>
            </Tabs>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={insertScore} columns={columns} />
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'center' }}>
            <Button type="primary">LƯU</Button>
            <Button type="warning">KHÓA ĐIỂM</Button>
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <InsertScorePage />
  </Page>
);



